import React from "react";
import { Experience } from "../../../datatypes/bindings";

import "./tech-stack-container.scss";
import { Tooltip } from "@mui/material";

interface TechStackContainerProps {
  title: string;
  stack: Experience[];
}

export const TechStackContainer: React.FunctionComponent<
  TechStackContainerProps
> = (props: TechStackContainerProps) => {
  return (
    <>
      <div className="tech-stack-container">
        <h3>{props.title}</h3>
        {props.stack.length > 0 ? (
          <nav>
            <ul>
              {props.stack.map((t: Experience, i: number) => (
                <li key={t.tech}>
                  <Tooltip
                    placement="top"
                    title={
                      new Date().getFullYear() - t.yearsOfExperience + " years"
                    }
                  >
                    <p>
                      {t.tech} {i < props.stack?.length - 1 ? "/" : ""}
                    </p>
                  </Tooltip>
                </li>
              ))}
            </ul>
          </nav>
        ) : null}
        {props.stack?.length === 0 ? <p>Loading</p> : null}
      </div>
    </>
  );
};
