export enum ENV {
  LOCAL,
  PROD,
}

export class ApiClient {
  private static baseURL: string;
  private static baseLocalURL: string = "http://localhost:8080";
  private static baseProdURL: string = "http://rrgzportfolio.us-east-1.elasticbeanstalk.com";
  private static headers: { [header: string]: string } = {
    "content-type": "application/json;charset=UTF-8",
  };

  private constructor() { }

  static setEnvMode(env: ENV) {
    if (env === ENV.LOCAL) {
      ApiClient.baseURL = ApiClient.baseLocalURL;
    } else {
      ApiClient.baseURL = ApiClient.baseProdURL;
    }
  }

  static async getProjects(): Promise<Response> {
    return fetch(`${ApiClient.baseURL}/projects`, {
      method: "GET",
      headers: ApiClient.headers,
    });
  }

  static async getUser(): Promise<Response> {
    return fetch(`${ApiClient.baseURL}/user/login`, {
      method: "POST",
      headers: ApiClient.headers,
      body: JSON.stringify({
        username: "Rene Rodriguez L",
        password: "pelemarley23",
      }),
    });
  }

  static async getProjectsByQuery(
    projectName?: string,
    projectTechs?: string[]
  ): Promise<Response> {
    return fetch(`${ApiClient.baseURL}/projects/query`, {
      method: "POST",
      headers: ApiClient.headers,
      body: JSON.stringify({
        name: projectName,
        technologies: projectTechs,
      }),
    });
  }

  static async getExperience(): Promise<Response> {
    return fetch(`${ApiClient.baseURL}/experience`, {
      method: 'GET',
      headers: ApiClient.headers,
    });
  }

  static async getLastTenUpdates(): Promise<Response> {
    return fetch(`${ApiClient.baseURL}/updates`, {
      method: 'GET',
      headers: ApiClient.headers,
    });
  }
}
