import React, { useEffect, useState } from "react";

import { SliderItem, SliderItemSide } from "./slideritem/slideritem";
import {
  SliderControl,
  SliderControlMobile,
} from "./slidercontrol/slidercontrol";
import { ApiClient } from "../../../datatypes/apiClient";
import { Updates } from "../../../datatypes/bindings";

import "./slider.scss";

interface IndexItemsManager {
  left?: number;
  center: number;
  right?: number;
}

export const Slider: React.FunctionComponent = () => {
  const [isLoading, setIsLoading] = useState<boolean>(true);
  const [lastUpdates, setLastUpdates] = useState<Updates[]>([]);
  const [indexItems, setIndexItems] = useState<IndexItemsManager>({
    center: 0,
  });

  useEffect(() => {
    let currentIndex: number = indexItems?.center ?? 0;
    let leftIndex: number | undefined = !lastUpdates[currentIndex - 1]
      ? undefined
      : currentIndex - 1;
    let rightIndex: number | undefined = !lastUpdates[currentIndex + 1]
      ? undefined
      : currentIndex + 1;
    setIndexItems({
      left: leftIndex,
      center: currentIndex,
      right: rightIndex,
    });
  }, [indexItems.center, isLoading]);

  useEffect(() => {
    ApiClient.getLastTenUpdates().then((res: Response) => {
      res.json().then((updates: Updates[]) => {
        setLastUpdates(updates);
        setIsLoading(false);
      });
    });
  }, []);

  const canPrev = () => {
    return indexItems && indexItems.center - 1 >= 0;
  };

  const canNext = () => {
    return indexItems && indexItems.center + 1 < lastUpdates.length;
  };

  const nextUpdate = (): void => {
    if (indexItems && canNext()) {
      setIndexItems({
        ...indexItems,
        center: indexItems.center + 1,
      });
    }
  };

  const prevUpdate = (): void => {
    if (indexItems && canPrev()) {
      setIndexItems({
        ...indexItems,
        center: indexItems.center - 1,
      });
    }
  };

  return (
    <>
      <div id="slider-component">
        {lastUpdates.length > 0 && !isLoading ? (
          <>
            <SliderControl
              canNext={canNext()}
              canPrev={canPrev()}
              prevUpdate={prevUpdate}
              nextUpdate={nextUpdate}
            />

            <div className="left-item">
              {indexItems?.left != undefined ? (
                <SliderItem
                  updates={lastUpdates[indexItems.left]}
                  side={SliderItemSide.LEFT}
                />
              ) : null}
            </div>
            <div className="center-item">
              {indexItems?.center != undefined ? (
                <SliderItem
                  updates={lastUpdates[indexItems.center]}
                  side={SliderItemSide.CENTER}
                />
              ) : null}
            </div>
            <div className="right-item">
              {indexItems?.right != undefined ? (
                <SliderItem
                  updates={lastUpdates[indexItems.right]}
                  side={SliderItemSide.RIGHT}
                />
              ) : null}
            </div>
          </>
        ) : null}
      </div>
      <SliderControlMobile
        currentIndex={indexItems.center}
        updatesSize={lastUpdates.length}
        canNext={canNext()}
        canPrev={canPrev()}
        prevUpdate={prevUpdate}
        nextUpdate={nextUpdate}
      />
    </>
  );
};
