import React from "react";
import { Grid } from "@mui/material";

import KeyboardDoubleArrowLeftIcon from "@mui/icons-material/KeyboardDoubleArrowLeft";
import KeyboardDoubleArrowRightIcon from "@mui/icons-material/KeyboardDoubleArrowRight";

import "./projectcontrol.scss";

interface ProjectControlProps {
  currentIndex: number;
  updatesSize: number;
  canPrev: boolean;
  canNext: boolean;
  nextUpdate: () => void;
  prevUpdate: () => void;
}

export const ProjectControl: React.FunctionComponent<ProjectControlProps> = (
  props: ProjectControlProps
) => {
  return (
    <Grid className="project-control" container>
      <Grid item xs={4} sm={4} md={4} lg={4} xl={4} textAlign="center">
        {props.canPrev ? (
          <button className="prev" onClick={() => props.prevUpdate()}>
            {<KeyboardDoubleArrowLeftIcon />}
          </button>
        ) : null}
      </Grid>
      <Grid item xs={4} sm={4} md={4} lg={4} xl={4} textAlign="center">
        <p>
          {props.currentIndex + 1} / {props.updatesSize}
        </p>
      </Grid>
      <Grid item xs={4} sm={4} md={4} lg={4} xl={4} textAlign="center">
        {props.canNext ? (
          <button className="next" onClick={() => props.nextUpdate()}>
            {<KeyboardDoubleArrowRightIcon />}
          </button>
        ) : null}
      </Grid>
    </Grid>
  );
};
