import React from "react";
import { Grid } from "@mui/material";
import { Updates } from "../../../../datatypes/bindings";

import EastIcon from "@mui/icons-material/East";

import "./slideritem.scss";

interface SliderItemProps {
  updates: Updates;
  side: SliderItemSide;
}

export enum SliderItemSide {
  LEFT,
  CENTER,
  RIGHT,
}

export const SliderItem: React.FunctionComponent<SliderItemProps> = (
  props: SliderItemProps
) => {
  const goTo = (url: string | undefined) => {
    if (url) {
      window.open(url, "_blank");
    }
  };
  const getContentPreview = () => {
    const desc = props.updates?.description;
    return (desc?.length > 30 ? desc.substring(0, 60) : desc) + "...";
  };

  const isCenterItem = () => props.side === SliderItemSide.CENTER;
  const isLeftItem = () => props.side === SliderItemSide.LEFT;
  const isRightItem = () => props.side === SliderItemSide.RIGHT;

  const getSliderInfoPadding = () => {
    return isCenterItem() || isRightItem()
      ? "20px 30px"
      : "20px 50px 20px 2px !important";
  };

  const getBackground = () => {
    if (isCenterItem()) {
      return {
        background: `center / cover no-repeat url("${props.updates.imgPreview}")`,
      };
    }
    return {
      backgroundColor: "black",
    };
  };

  return (
    <div className="slider-item" style={getBackground()}>
      <Grid container spacing={0}>
        {isCenterItem() || isLeftItem() ? (
          <Grid item xs={12} sm={12} md={4} lg={4} xl={4}></Grid>
        ) : null}
        <Grid
          className="slider-item-info"
          item
          xs={isCenterItem() || isLeftItem() ? 12 : 12}
          sm={isCenterItem() || isLeftItem() ? 12 : 12}
          md={isCenterItem() || isLeftItem() ? 8 : 12}
          lg={isCenterItem() || isLeftItem() ? 8 : 12}
          xl={isCenterItem() || isLeftItem() ? 8 : 12}
          padding={getSliderInfoPadding()}
          borderRadius={isCenterItem() ? "0px 20px 20px 0px" : "20px"}
        >
          <h3>{props.updates.title}</h3>
          <p>{getContentPreview()}</p>
          {props.updates.url ? (
            <div className="item-actions">
              <button
                className="read-more"
                onClick={() => goTo(props.updates.url)}
              >
                Read more
              </button>
              <button
                className="read-more-go"
                onClick={() => goTo(props.updates.url)}
              >
                <EastIcon />
              </button>
            </div>
          ) : null}
        </Grid>
      </Grid>
    </div>
  );
};
