import React from "react";

import { Grid } from "@mui/material";

import KeyboardDoubleArrowLeftIcon from "@mui/icons-material/KeyboardDoubleArrowLeft";
import KeyboardDoubleArrowRightIcon from "@mui/icons-material/KeyboardDoubleArrowRight";

import "./slidercontrol.scss";

interface SliderControlProps {
  canPrev: boolean;
  canNext: boolean;
  nextUpdate: () => void;
  prevUpdate: () => void;
}

export const SliderControl: React.FunctionComponent<SliderControlProps> = (
  props: SliderControlProps
) => {
  return (
    <Grid
      className="slider-control"
      container
      sx={{ position: "absolute", paddingX: "3vw" }}
    >
      <Grid item xs={6} sm={6} md={6} lg={6} xl={6} textAlign="left">
        {props.canPrev ? (
          <button className="prev" onClick={() => props.prevUpdate()}>
            {<KeyboardDoubleArrowLeftIcon />}
          </button>
        ) : null}
      </Grid>
      <Grid item xs={6} sm={6} md={6} lg={6} xl={6} textAlign="right">
        {props.canNext ? (
          <button className="next" onClick={() => props.nextUpdate()}>
            {<KeyboardDoubleArrowRightIcon />}
          </button>
        ) : null}
      </Grid>
    </Grid>
  );
};

interface SliderControlMobileProps {
  currentIndex: number;
  updatesSize: number;
  canPrev: boolean;
  canNext: boolean;
  nextUpdate: () => void;
  prevUpdate: () => void;
}

export const SliderControlMobile: React.FunctionComponent<
  SliderControlMobileProps
> = (props: SliderControlMobileProps) => {
  return (
    <Grid className="slider-control-mobile" container>
      <Grid item xs={4} sm={4} md={4} lg={4} xl={4} textAlign="center">
        {props.canPrev ? (
          <button className="prev" onClick={() => props.prevUpdate()}>
            {<KeyboardDoubleArrowLeftIcon />}
          </button>
        ) : null}
      </Grid>
      <Grid item xs={4} sm={4} md={4} lg={4} xl={4} textAlign="center">
        <p>
          {props.currentIndex + 1} / {props.updatesSize}
        </p>
      </Grid>
      <Grid item xs={4} sm={4} md={4} lg={4} xl={4} textAlign="center">
        {props.canNext ? (
          <button className="next" onClick={() => props.nextUpdate()}>
            {<KeyboardDoubleArrowRightIcon />}
          </button>
        ) : null}
      </Grid>
    </Grid>
  );
};
