import React, { useEffect, useState } from "react";
import { Grid } from "@mui/material";
import { TechStackContainer } from "./tech-stack-container/tech-stack-container";

import ProfilePic from "../../assets/imgs/photo.png";
import GithubLogo from "../../assets/imgs/github-logo.png";

import { Experience, TechCategory } from "../../datatypes/bindings";
import { ApiClient } from "../../datatypes/apiClient";

import "./myself.scss";

export const MySelf: React.FunctionComponent = () => {
  const [frontendTech, setFrontendTech] = useState<Experience[]>([]);
  const [backendTech, setBackendTech] = useState<Experience[]>([]);
  const [databaseTech, setDatabaseTech] = useState<Experience[]>([]);
  const [devOpsTech, setDevOpsTech] = useState<Experience[]>([]);
  useEffect(() => {
    ApiClient.getExperience().then((res) => {
      res.json().then((exps: Experience[]) => {
        setFrontendTech(
          exps.filter(
            (t) =>
              t.category === TechCategory.FRONTEND ||
              t.category === TechCategory.FRONTBACK
          )
        );
        setBackendTech(
          exps.filter(
            (t) =>
              t.category === TechCategory.BACKEND ||
              t.category === TechCategory.FRONTBACK
          )
        );
        setDatabaseTech(
          exps.filter((t) => t.category === TechCategory.DATABASE)
        );
        setDevOpsTech(exps.filter((t) => t.category === TechCategory.DEVOPS));
      });
    });
  }, []);
  return (
    <>
      <Grid id="myself-component" container spacing={0}>
        <Grid container item xs={12} sm={12} md={6} lg={6} xl={6}>
          <Grid item xs={12} sm={12} md={12} lg={12} xl={12}>
            <TechStackContainer title="Frontend" stack={frontendTech} />
          </Grid>
          <Grid item xs={12} sm={12} md={12} lg={12} xl={12}>
            <TechStackContainer title="Backend" stack={backendTech} />
          </Grid>
          <Grid container spacing={0}>
            <Grid item xs={12} sm={8} md={8} lg={8}>
              <TechStackContainer title="Database" stack={databaseTech} />
            </Grid>
            <Grid
              container
              item
              xs={12}
              sm={4}
              md={4}
              lg={4}
              xl={4}
              alignItems="center"
              justifyContent="center"
            >
              <a href="https://www.github.com/rennyjr98" target="_blank">
                <img src={GithubLogo} alt="github logo" width="50vw" />
              </a>
            </Grid>
          </Grid>
          <Grid
            container
            item
            xs={12}
            sm={12}
            md={12}
            lg={12}
            xl={12}
            spacing={0}
          >
            <Grid item xs={12} sm={6} md={6} lg={6} xl={6}>
              <p className="tech-msg">
                Some of my <b>favorite technologies</b>, <b>topics</b>, or{" "}
                <b>tools</b> that I worked with
              </p>
            </Grid>
            <Grid item xs={12} sm={6} md={6} lg={6} xl={6}>
              <TechStackContainer title="Devops" stack={devOpsTech} />
            </Grid>
          </Grid>
        </Grid>
        <Grid
          className="grid-img"
          item
          xs={12}
          sm={12}
          md={6}
          lg={6}
          xl={6}
          textAlign="center"
        >
          <div className="spacing-box"></div>
          <img
            className="profile-pic"
            src={ProfilePic}
            alt="profile pic"
            width="100%"
          />
        </Grid>
      </Grid>
    </>
  );
};
