import React, { useEffect, useState } from "react";

import { Project } from "../../datatypes/bindings";
import { ApiClient } from "../../datatypes/apiClient";
import { Chip, Grid } from "@mui/material";
import { ProjectControl } from "./projectcontrol/projectcontrol";
import LaunchIcon from "@mui/icons-material/Launch";

import "./projects.scss";

export const Projects: React.FunctionComponent = () => {
  const [projects, setProjects] = useState<Project[]>([]);
  const [currentIndex, setCurrentIndex] = useState<number>(0);
  useEffect(() => {
    ApiClient.getProjects().then((res) => {
      res.json().then((projs: Project[]) => {
        setProjects(projs);
      });
    });
  }, []);

  const getProject = () => projects[currentIndex];

  const canNext = () => currentIndex < projects.length - 1;
  const canPrev = () => currentIndex > 0;

  const prevProject = () => setCurrentIndex(currentIndex - 1);
  const nextProject = () => setCurrentIndex(currentIndex + 1);

  return (
    <div id="projects-component">
      <h2>Projects</h2>

      {projects[currentIndex] ? (
        <Grid className="c-project-container" container>
          <Grid item xs={12} sm={6} md={6} lg={6} xl={6}>
            <div
              className="project-img"
              style={{
                background: `center / cover no-repeat url("${
                  getProject().img
                }")`,
              }}
            ></div>
          </Grid>
          <Grid item xs={12} sm={6} md={6} lg={6} xl={6} paddingLeft="3.5vw">
            <h3>{getProject().name}</h3>
            <p className="project-desc">{getProject().description}</p>

            {getProject().technologies.map((tech) => (
              <Chip key={tech} label={tech} sx={{ marginX: ".2vw" }} />
            ))}

            {getProject().url ? (
              <div className="action">
                <a href={getProject().url} target="_blank">
                  <span>Open</span>
                  <LaunchIcon />
                </a>
              </div>
            ) : null}
          </Grid>
        </Grid>
      ) : null}

      <ProjectControl
        canNext={canNext()}
        canPrev={canPrev()}
        currentIndex={currentIndex}
        nextUpdate={nextProject}
        prevUpdate={prevProject}
        updatesSize={projects.length}
        key="project-control"
      />
    </div>
  );
};
